body {
  margin: 0;
  padding: 0;
  font-family: "Segoe UI", "Roboto", "Oxygen", sans-serif;
  color: rgb(36, 35, 35);
}

.screenMain {
  width: 85%;
  min-height: 400px;
  margin: 20px auto;
  border: 1px solid rgb(146, 146, 146);
  display: grid;
  grid-template-rows: 50px auto;
  grid-template-columns: 1fr 1fr;
  grid-template-areas:
    "header header"
    "recipes detailRecipe";
}

input {
  border: solid 1px darkgray;
  margin-right: 5px;
  border-radius: 5px;
  width: 75%;
  text-transform: uppercase;
}

input:focus{
  outline: none;
}

button {
  border-radius: 5px;
  background: #7bd2d1;
  color: white;
  text-transform: uppercase;
  font-size: 9px;
  font-weight: bold;
  padding: 3px 7px 2px 7px;
}

button:focus{
  outline: none;
}

.header {
  grid-area: header;
  background: #7bd2d1;
}

.recipes {
  grid-area: recipes;
  padding: 1em;
  border-right: 1px solid rgb(146, 146, 146);

}

.detailRecipe {
  grid-area: detailRecipe;
  padding-left: 15px;
  padding-right: 5px;
  display: grid;
  grid-template-rows: 60px auto;
  grid-template-columns: 30% auto;
  grid-template-areas:
    "recipeName recipeName"
    "ingredients instructions";

}
.title{
  font-size:10px;
  font-weight: bold;
  text-transform: uppercase;
}

img{
  float: left;
  margin-right: 12px;
}

.recipeName {
  margin-top: 20px;
  grid-area: recipeName;
  text-transform: uppercase;
  font-weight: bolder;
  font-size: 1.2em;
  color:#5c4033;
}

.ingredients {
  grid-area: ingredients;
  color:#5c4033;
  font-size: 10px;
}

.instructions {
  grid-area: instructions;
  color:#5c4033;
  font-size: 10px;
}

.recipeList {
  border: 1px solid darkgrey;
  padding: 5px;
  text-transform: uppercase;
}

.searchDiv {
  width: 95%;
}

@media only screen and (max-width: 600px) {
  .screenMain {
    width: 95%;
    height: auto;
    margin: 5px;
    border: 1px solid rgb(146, 146, 146);
    display: grid;
    grid-template-rows: 1fr;
    grid-template-columns: 1fr;
    grid-template-areas:
      "header"
      "recipes"
      "detailRecipe";
  }
  .header{
    height:50px;
  }

  .recipes {
    border-right:0px; 
    border-bottom: 1px solid rgb(146, 146, 146);
  }

  .recipeName {
    font-size: 16px;
  }
}

